.header {
    width: 100%;
    padding: 2rem;
    display: flex;
    justify-content:flex-end;
    align-items: center;

    background: #020202;
    background: -webkit-linear-gradient(top, #020202, #0f0f0f);
    background: -moz-linear-gradient(top, #020202, #0f0f0f);
    background: linear-gradient(to bottom, #020202, #0f0f0f);


    .btn-login {
        margin-right: 4rem;
        margin-left: 2rem;
        color: black;
        background-color: #08FDD8;
        padding: 1rem;
        border: none;
        transition: all .2s linear;

        &:hover {
            background-color: lighten($color: #08FDD8, $amount: 30);
        }
    }

    p {
        color: red;
    }

    .admin {
        color: white;
    }
}