.login {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 5rem;

    input {
        margin-bottom: 2rem;
        width: 300px;
        background-color:transparent;
        border: 1px solid #08FDD8;
        color: white;
        padding: .5rem 1rem;
        border-radius: 10px;

        &.error {
            border: 1px solid red;
        }
    }

    button {
        margin-right: 4rem;
        margin-left: 2rem;
        color: black;
        background-color: #08FDD8;
        padding: 1rem;
        border: none;
        transition: all .2s linear;

        &:hover {
            background-color: lighten($color: #08FDD8, $amount: 30);
        }
    }
}