.add-user {
   width: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   margin-top: 3rem;

   input, textarea {
       width: 500px;
       border: 1px solid white;
       border-radius: 5px;
       margin-bottom: 1rem;
       padding: .5rem 1rem;
       color: #08FDD8;
       background-color: transparent;
   }

   textarea {
       min-height: 50px;
       max-height: 300px;
   }

   button {
       padding: 1rem;
       background-color: #08FDD8;
       color: black;
       border: none;
       transition: all .2s linear;

       &:hover {
           background-color:lighten($color: #08FDD8, $amount: 30);
       }
   }
}