.user {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 5px;
    color: black;
    font-weight: 500;
    font-size: 1.4rem;
    margin-top: 2rem;

    p {
        border-right: 1px solid black;
        padding: 1rem 0;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        &:last-child {
            border-right: none;
        }
    }

    .user-id {
        max-width: 70px;
        width: 70px;
        text-align: center;
    }

    .user-name {
        max-width: 200px;
        width: 200px;
        text-align: center;
    }

    .user-email {
        max-width: 250px;
        width: 250px;
        text-align: center;
    }

    .user-text {
        max-width: 250px;
        width: 250px;
        text-align: center;
        cursor: help;
    }

    .user-status {
        position: relative;
        max-width: 110px;
        width: 110px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: help;

        &:hover {
            .status-text {
                opacity: 1;
            }
        }

        .status-text {
            position: absolute;
            top: 50%;
            right: -15rem;
            width: 120px;
            padding: .5rem;
            border-radius: 10px;
            background-color: #fff;
            transform: translateY(-50%);
            opacity: 0;
            transition: all .2s linear;
        }

        .status-0 {
            display: block;
            width: 15px;
            height: 15px;
            background-color: red;
            border-radius: 50%;
        }

        .status-10 {
            display: block;
            width: 15px;
            height: 15px;
            background-color: green;
            border-radius: 50%;
        }

        .status-1 {
            display: block;
            width: 15px;
            height: 15px;
            background-color: orange;
            border-radius: 50%;
        }

        .status-11 {
            display: block;
            width: 15px;
            height: 15px;
            background-color: blue;
            border-radius: 50%;
        }
    }
}