.app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow: hidden;
    background: #0D0F0E;
    background: -webkit-linear-gradient(top, #0D0F0E, #1C1C1B);
    background: -moz-linear-gradient(top, #0D0F0E, #1C1C1B);
    background: linear-gradient(to bottom, #0D0F0E, #1C1C1B);
    

    .container {
        display: flex;
        justify-content: center;
        padding: 2rem 30rem;
    }
}