.main {
   display: flex;
   justify-content: center;
   flex-direction: column;

   .preloader {
       display: flex;
       justify-content: center;
       align-items: center;
   }

   .users-filter {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 5px;
    color: black;
    font-weight: 700;
    font-size: 1.5rem;

        p {
            position: relative;
            border-right: 1px solid black;
            padding: 1rem 0;
            cursor: pointer;

            &:last-child {
                border-right: none;
            }

            &:hover:after{
                opacity: 1;
            }

            &:after {
                opacity: 0;
                position: absolute;
                content: '';
                width: 8px;
                height: 8px;
                top: 50%;
                background-color: transparent;
                border-bottom: 1px solid red;
                border-right: 1px solid red;
                transition: all .2s linear;
            }
        }

        .id {
            position: relative;
            max-width: 70px;
            width: 70px;
            text-align: center;

            &:after {
                right: 1rem;
                transform: translateY(-75%) rotate(45deg);
            }

            &.active {
                &:after {
                    transform: translateY(-50%) rotate(-135deg);
                }
            }
        }

        .username {
            position: relative;
            max-width: 200px;
            width: 200px;
            text-align: center;
            &:after {
                right: 3rem;
                transform: translateY(-75%) rotate(45deg);
            }

            &.active {
                &:after {
                    transform: translateY(-50%) rotate(-135deg);
                }
            }
        }

        .email {
            position: relative;
            max-width: 250px;
            width: 250px;
            text-align: center;

            &:after {
                right: 3rem;
                transform: translateY(-75%) rotate(45deg);
            }

            &.active {
                &:after {
                    transform: translateY(-50%) rotate(-135deg);
                }
            }
        }

        .text {
            position: relative;
            max-width: 250px;
            width: 250px;
            text-align: center;

            &:hover:after {
                opacity: 0;
            }
        }

        .status {
            position: relative;
            max-width: 100px;
            width: 100px;
            text-align: left;
            padding-left: 1rem;

            
            &:after {
                right: 2rem;
                transform: translateY(-75%) rotate(45deg);
            }

            &.active {
                &:after {
                    transform: translateY(-50%) rotate(-135deg);
                }
            }
        }
   }

   .paginator {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 4rem;

        .btn-page {
            border: none;
            color: white;
            margin-right: 1rem;
            margin-left: 1rem;
        }

        .next-page {
            width: 7px;
            height: 7px;
            border: none;
            background-color: transparent;
            border-bottom: 1px solid #08FDD8;
            border-right: 1px solid #08FDD8;
            transform: rotate(-45deg);
            cursor: pointer;

            &:hover {
                border-bottom: 1px solid lighten($color: #08FDD8, $amount: 30);
                border-right: 1px solid lighten($color: #08FDD8, $amount: 30);
            }
        }

        .prev-page {
            width: 7px;
            height: 7px;
            background-color: transparent;
            border: none;
            border-bottom: 1px solid #08FDD8;
            border-right: 1px solid #08FDD8;
            transform: rotate(-225deg);
            cursor: pointer;

            &:hover {
                border-bottom: 1px solid lighten($color: #08FDD8, $amount: 30);
                border-right: 1px solid lighten($color: #08FDD8, $amount: 30);
            }
        }
   }
}